import React, { useState } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Dialog, DialogContent, DialogTitle } from "@/components/ui/dialog";
import { Switch } from "@/components/ui/switch";
import { UserRound, Stethoscope, Mail, Lock, User } from "lucide-react";

const AuthModal = ({ isOpen, onClose }) => {
  const [isDoctor, setIsDoctor] = useState(false);
  const [isLogin, setIsLogin] = useState(true);

  const handleToggleUserType = () => {
    setIsDoctor(!isDoctor);
  };

  const handleToggleAuthMode = () => {
    setIsLogin(!isLogin);
  };

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="sm:max-w-md rounded-lg p-0 overflow-hidden">
        {/* Header with gradient */}
        <div className="bg-gradient-to-r from-[#5636e5] to-[#7a60ff] p-6 text-white">
          <DialogTitle className="text-xl font-bold text-center mb-4">
            {isLogin ? "Welcome Back" : "Create Account"}
          </DialogTitle>
          
          {/* User type toggle */}
          <div className="flex justify-center items-center gap-3 bg-white/10 p-3 rounded-lg">
            <div className="flex items-center gap-2">
              <UserRound size={18} className={!isDoctor ? "text-white" : "text-white/60"} />
              <span className={!isDoctor ? "font-medium" : "text-white/60"}>Patient</span>
            </div>
            
            <Switch
              checked={isDoctor}
              onCheckedChange={handleToggleUserType}
              className="data-[state=checked]:bg-white data-[state=unchecked]:bg-white/40"
            />
            
            <div className="flex items-center gap-2">
              <Stethoscope size={18} className={isDoctor ? "text-white" : "text-white/60"} />
              <span className={isDoctor ? "font-medium" : "text-white/60"}>Doctor</span>
            </div>
          </div>
        </div>

        {/* Form section */}
        <div className="p-6">
          {isLogin ? (
            // Login Form
            <form className="space-y-4">
              <div className="space-y-2">
                <Label htmlFor="email" className="text-gray-700">Email Address</Label>
                <div className="relative">
                  <Mail className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
                  <Input 
                    id="email" 
                    type="email" 
                    placeholder="Enter your email" 
                    className="pl-10 border-gray-200 focus:border-[#5636e5] focus:ring-[#5636e5]" 
                  />
                </div>
              </div>
              
              <div className="space-y-2">
                <div className="flex justify-between">
                  <Label htmlFor="password" className="text-gray-700">Password</Label>
                  <a href="#" className="text-sm text-[#5636e5] hover:underline">Forgot password?</a>
                </div>
                <div className="relative">
                  <Lock className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
                  <Input 
                    id="password" 
                    type="password" 
                    placeholder="Enter your password" 
                    className="pl-10 border-gray-200 focus:border-[#5636e5] focus:ring-[#5636e5]" 
                  />
                </div>
              </div>
              
              <Button 
                type="submit" 
                className="w-full bg-gradient-to-r from-[#5636e5] to-[#7a60ff] hover:opacity-90 py-6"
              >
                Login
              </Button>
            </form>
          ) : (
            // Signup Form
            <form className="space-y-4">
              <div className="space-y-2">
                <Label htmlFor="fullName" className="text-gray-700">Full Name</Label>
                <div className="relative">
                  <User className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
                  <Input 
                    id="fullName" 
                    type="text" 
                    placeholder="Enter your full name" 
                    className="pl-10 border-gray-200 focus:border-[#5636e5] focus:ring-[#5636e5]" 
                  />
                </div>
              </div>
              
              <div className="space-y-2">
                <Label htmlFor="signupEmail" className="text-gray-700">Email Address</Label>
                <div className="relative">
                  <Mail className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
                  <Input 
                    id="signupEmail" 
                    type="email" 
                    placeholder="Enter your email" 
                    className="pl-10 border-gray-200 focus:border-[#5636e5] focus:ring-[#5636e5]" 
                  />
                </div>
              </div>
              
              <div className="space-y-2">
                <Label htmlFor="signupPassword" className="text-gray-700">Password</Label>
                <div className="relative">
                  <Lock className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
                  <Input 
                    id="signupPassword" 
                    type="password" 
                    placeholder="Create a password" 
                    className="pl-10 border-gray-200 focus:border-[#5636e5] focus:ring-[#5636e5]" 
                  />
                </div>
              </div>
              
              {isDoctor && (
                <div className="space-y-2">
                  <Label htmlFor="specialization" className="text-gray-700">Specialization</Label>
                  <div className="relative">
                    <Stethoscope className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
                    <Input 
                      id="specialization" 
                      type="text" 
                      placeholder="Enter your specialization" 
                      className="pl-10 border-gray-200 focus:border-[#5636e5] focus:ring-[#5636e5]" 
                    />
                  </div>
                </div>
              )}
              
              <Button 
                type="submit" 
                className="w-full bg-gradient-to-r from-[#5636e5] to-[#7a60ff] hover:opacity-90 py-6"
              >
                Create Account
              </Button>
            </form>
          )}

          {/* Toggle between login and signup */}
          <div className="mt-6 text-center">
            <p className="text-gray-600">
              {isLogin ? "Don't have an account? " : "Already have an account? "}
              <button 
                type="button"
                onClick={handleToggleAuthMode}
                className="text-[#5636e5] font-medium hover:underline focus:outline-none"
              >
                {isLogin ? "Sign up" : "Login"}
              </button>
            </p>
          </div>
        </div>
      </DialogContent>
    </Dialog>
  );
};

export default AuthModal;